import { useState } from "react";
import { useMutation } from "@tanstack/react-query";
import { createDojang } from "../../../../api/Dojang/dojangApi";
import { useNavigate } from "react-router-dom";
import ModalAddress from "../../../../components/common/PostCode/ModalAddress";
import BlackBtnSM from "../../../../components/UI/Atoms/Button/BlackBtnSM";
import WhiteBtnSM from "../../../../components/UI/Atoms/Button/WhiteBtnSM";
import ProfileEditUploadBtn from "../../../../components/UI/Atoms/Icon/ProfileEditUploadBtn";
import { useModal } from "../../../../hooks/basic/useModal";
import { useFile } from "../../../../hooks/file/useFile";
import { useInputs } from "../../../../hooks/input/useInputs";
import { getFormData } from "../../../../utils/basic/getFormData";
import { formDataDebug } from "../../../../utils/debug/debug";
import BRImage from "../Dojang/BRImage";
import InfoBox from "../Dojang/DojangInfo/components/InfoBox";
import { InfoInput, InfoLabel } from "../Dojang/InfoLable";
import { Col, Row } from "../Student/component/StudentInfo/StudentForm/StudentForm";


const CreateDojang = () => {
  const navigate = useNavigate();

  // const fileInputRef = useRef();

  // const clickUpload = () => {
  //   fileInputRef.current.click();
  // };

  // const [previewBR, setPreviewBR] = useState();
  // const [fileBR, setFileBR] = useState();
  // const [previewLogo, setPreviewLogo] = useState();
  // const [fileLogo, setFileLogo] = useState();

  const [editData, setEditData, handleChange] = useInputs({
    name: '',
    phone_number: '',
    address_name: '',
    address_detail: '',
    BR: '',
    BR_number: '',
    // logo_img: '',
  });

  const [open, openFn, closeFn] = useModal();

  const { isLoading, mutate } = useMutation((body) => createDojang(body), {
    onSuccess: () => setTimeout(1000, window.location.reload()),
    onError: () => alert('error!'),
  });

  const [handleFileInput, fileInputRef, clickUpload] = useFile({
    data: editData,
    urlKey: 'previewUrl',
    fileKey: 'BR_img',
    set: setEditData,
  });

  const postData = () => {
    const { BR, BR_img, previewUrl, ...postData } = editData;
    const formData = getFormData([
      ...Object.entries(postData),
      ['BR_img', BR_img],
      // ['logo', logo]
    ]);
    formDataDebug(formData);
    // mutate(formData);
  };

  const handleCancel = () => {
    navigate('/manage/dojang');
  };

  const handleSave = (e) => {
    e.preventDefault();
    postData();
  };

  // const handleFileInput = (e) => {
  //   const fileList = e.target.files[0];
  //   const currentFile = fileList;
  //   const currentUrl = URL.createObjectURL(fileList);
  // };
    // useDebug(editData)
  return (
    <>
      <PageContainer width="sm">
        <InfoBox
          title="도장 사업자 정보"
          className="w-[500px] m-auto"
          btn={
            <div className="flex gap-2 mb-[20px]">
              <WhiteBtnSM className="w-[70px] h-[30px]" onClick={handleCancel}>
                취소
              </WhiteBtnSM>
              <BlackBtnSM className="w-[70px] h-[30px]" onClick={handleSave}>
                저장
              </BlackBtnSM>
            </div>
          }
        >
          <div>
            <Row>
              <Col className="mb-[10px]">
                <InfoLabel>도장 이름</InfoLabel>
                <div>
                  <InfoInput name="name" value={editData.name || ''} onChange={handleChange} />
                </div>
              </Col>
            </Row>
            <Row>
              <Col className="mb-[10px]">
                <InfoLabel>도장 전화번호</InfoLabel>
                <div>
                  <InfoInput
                    name="phone_number"
                    value={editData.phone_number || ''}
                    onChange={handleChange}
                  />
                </div>
              </Col>
            </Row>
            <Row>
              <Col className="mb-[10px]">
                <InfoLabel>도장 주소</InfoLabel>
                <div>
                  <InfoInput
                    name="address_name"
                    placeholder="주소 검색"
                    value={editData.address_name || ''}
                    onChange={handleChange}
                    onClick={openFn}
                  />
                  <ModalAddress open={open} onClose={closeFn} setValue={setEditData}></ModalAddress>
                </div>
              </Col>
            </Row>
            <Row>
              <Col className="mb-[10px]">
                <InfoLabel></InfoLabel>
                <div>
                  <InfoInput
                    name="address_detail"
                    placeholder="상세 주소"
                    value={editData.address_detail || ''}
                    onChange={handleChange}
                  />
                </div>
              </Col>
            </Row>
            <Row>
              <Col className="mb-[10px]">
                <InfoLabel>사업자 등록번호</InfoLabel>
                <div>
                  <InfoInput name="BR_number" value={editData.BR_number || ''} onChange={handleChange} />
                </div>
              </Col>
            </Row>
            <Row>
              <Col className="mb-[10px]">
                <InfoLabel>사업자 등록증</InfoLabel>
                <div className="flex flex-col items-center relative">
                  <BRImage
                    size="140px"
                    urlKey={editData.BR || ''}
                    previewUrl={editData.previewUrl || ''}
                    onChange={handleFileInput}
                    ref={fileInputRef}
                  />
                  {/* <input
                    size='140px'
                    type="file"
                    accept="image/*"
                    ref={fileInputRef}
                    onChange={handleFileInput}
                    className="hidden"
                  /> */}
                  <ProfileEditUploadBtn onClick={clickUpload}></ProfileEditUploadBtn>
                </div>
              </Col>
            </Row>
            {/* <Row>
              <Col>
                <div className="flex bg-[#f2f2f2] p-[10px]">
                  <div className="flex flex-col">
                    <InfoLabel>도장 로고</InfoLabel>
                    <div>
                      <div className="flex gap-2 mb-[5px]">
                        <LogoUploadBtn onClick={clickUpload}>+</LogoUploadBtn>
                      </div>
                    </div>
                  </div>
                  <div className="w-[354px] h-[250px] bg-[#ffffff]">
                    <LogoImage
                      urlKey={editData?.logo_img}
                      previewUrl={editData?.previewUrl}
                      onChange={handleFileInput}
                      ref={fileInputRef}
                    ></LogoImage>
                  </div>
                </div>
              </Col>
            </Row> */}
          </div>
        </InfoBox>
      </PageContainer>
    </>
  );
};

export default CreateDojang;
