import { useRef } from "react"

export const useFile = ({ data, urlKey, fileKey, set }) => {
  const fileInputRef = useRef()

  const clickUpload = () => {
    fileInputRef.current.click()
  }

  const handleFileInput = (e) => {
    const fileList = e.target.files
    const currentFile = fileList[0]
    const currentUrl = URL.createObjectURL(fileList[0])
    
    const temp = [
      urlKey ? 
        { ...data,  
          [fileKey]: currentFile,
          [urlKey]: currentUrl,
        }
      :
        { ...data,  
          [fileKey]: currentFile,
        }
      
    
      ]
    set({...temp})
  }

  return [ handleFileInput, fileInputRef, clickUpload ]
}
